<template>
    <div>
        <v-toolbar color="primary" app
            style="color: #E8E8E8"
        >
            <v-btn icon
                @click.stop="drawer = !drawer"
            >
                <v-icon
                    color="#E8E8E8"
                >list</v-icon>
            </v-btn>

            <v-toolbar-title v-if="page === 'dashboard'">
                {{ user.property }} Dashboard
            </v-toolbar-title>

            <v-toolbar-title v-if="page === 'user'">
                {{ user.name }}
            </v-toolbar-title>

            <v-spacer></v-spacer>

            <v-btn icon>
                <v-icon class="mr-0"
                    color="#E8E8E8"
                >
                    fas fa-calendar
                </v-icon>
            </v-btn>

            <v-toolbar-title
                style="font-size: 12pt;"
                class="ml-0 mt-1 mr-2"
            >
                {{ day }}
            </v-toolbar-title>

            <v-btn icon>
                <v-icon class="mr-0"
                    color="#E8E8E8"
                >
                    fas fa-clock
                </v-icon>
            </v-btn>

            <v-toolbar-title
                style="font-size: 12pt;"
                class="ml-0 mt-1 mr-2"
            >
                {{ now }}
            </v-toolbar-title>

            <v-tooltip left v-if="page==='user' || page==='error'">
                <v-btn icon
                    to="/dashboard"
                    slot="activator"
                    class="ml-0 pl-0"

                >
                    <v-icon
                        color="#E8E8E8"
                    >
                        fas fa-home
                    </v-icon>
                </v-btn>
                <span>Back To Dashboard</span>
            </v-tooltip>

            </v-toolbar>
        <v-navigation-drawer
            v-model="drawer"
            temporary
            app
        >

            <v-list class="pa-1">
                <v-list-tile avatar>
                <v-list-tile-avatar>
                    <img :src="user.avatar">
                </v-list-tile-avatar>

                <v-list-tile-content>
                    <v-list-tile-title>
                    {{ user.name }}
                    </v-list-tile-title>
                </v-list-tile-content>
                </v-list-tile>
            </v-list>

            <v-list class="pt-0" dense>
                <v-divider></v-divider>

                <v-list-tile
                    v-for="opt in options"
                    :key="opt.type"
                    :to="opt.link"
                >

                <v-list-tile-action>
                    <v-icon color="primary">{{ opt.icon }}</v-icon>
                </v-list-tile-action>

                <v-list-tile-content>
                    <v-list-tile-title>{{ opt.type }}</v-list-tile-title>
                </v-list-tile-content>
                </v-list-tile>
            </v-list>
        </v-navigation-drawer>
    </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';

export default {
  name: 'Toolbar',
  props: {
    page: String,
  },
  computed: {
    ...mapState([
      'user',
    ]),
    ...mapGetters([
      'GET_DARK',
    ]),
  },
  data: () => ({
    options: [
      { type: 'My Profile', icon: 'fas fa-user-circle', link: '/user/profile' },
      { type: 'Dashboard Settings', icon: 'fas fa-grip-horizontal', link: '/user/dashboard_settings' },
      { type: 'Property Settings', icon: 'fas fa-image', link: '/user/property' },
      { type: 'Privacy & Access Settings', icon: 'fas fa-lock-alt', link: '/user/privacy' },
      { type: 'Feedback', icon: 'fas fa-comment-alt-dots', link: '/user/feedback' },
      { type: 'Logout', icon: 'fas fa-sign-out', link: '/user/logout' },
    ],

    drawer: false,
    now: new Date(),
    day: new Date(),
  }),

  created() {
    setInterval(() => this.now = (new Date()).toLocaleTimeString('en-AU'));

    setInterval(() => this.day = (new Date()).toLocaleDateString('en-AU'));
  },

  methods: {
    ...mapActions([
      'DARK_MODE',
    ]),
  },
};

</script>
